<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.msgId"
        placeholder="订单编号"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-select
        v-model="listQuery.accessPointId"
        class="filter-item"
        placeholder="API"
        clearable
      >
        <el-option
          v-for="item in apiOptions"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        />
      </el-select>
      <el-select
        v-model="listQuery.status"
        class="filter-item"
        placeholder="状态"
        clearable
      >
        <el-option
          v-for="item in statusTypeOptions"
          :key="item.value"
          :label="item.lable"
          :value="item.value"
        />
      </el-select>
      <el-date-picker
        v-model="date"
        type="date"
        placeholder="Please pick a date"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        搜索
      </el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="ID" width="95">
        <template slot-scope="scope">
          {{ scope.$index }}
        </template>
      </el-table-column>
      <el-table-column label="订单号" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.msgId }}</span>
        </template>
      </el-table-column>
      <el-table-column label="API名称" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.accessPointName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="日期" align="center">
        <template slot-scope="scope">
          <span>{{
            scope.row.ctime | timestampFilter("{y}-{m}-{d} {h}:{i}:{s}")
          }}</span>
        </template>
      </el-table-column>
      <el-table-column label="价格" align="center">
        <template slot-scope="scope">
          <span>{{ parseFloat(scope.row.price) / 100.0 }}</span>
        </template>
      </el-table-column>
      <el-table-column
        class-name="status-col"
        label="状态"
        width="110"
        align="center"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.status | statusFilter }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <el-button type="primary" size="mini" @click="handle(row)">
            详情
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
  </div>
</template>

<script>
import { paymentList } from "@/api/record";
import { list } from "@/api/accessPoint";
import { parseTime } from "@/utils";
import waves from "@/directive/waves"; // waves directive
import Pagination from "@/components/Pagination";

const statusTypeOptions = [
  { value: 1, lable: "正常" },
  { value: 2, lable: "失败" }
];

// arr to obj, such as { CN : "China", US : "USA" }
const statusTypeKeyValue = statusTypeOptions.reduce((acc, cur) => {
  acc[cur.value] = cur.lable;
  return acc;
}, {});

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    statusFilter(status) {
      return statusTypeKeyValue[status];
    },
    timestampFilter(time, fm) {
      return parseTime(time, fm);
    }
  },
  data() {
    return {
      total: 0,
      listQuery: {
        msgId: undefined,
        accessPointId: undefined,
        status: undefined,
        dateStr: "",
        pageNo: 1,
        pageSize: 20
      },
      date: new Date(),
      list: null,
      listLoading: true,
      apiOptions: [],
      statusTypeOptions
    };
  },
  created() {
    list({ status: 1 }).then(response => {
      if (response.code === 0) {
        const data = response.data;
        this.apiOptions = data.record;
      } else {
        this.$message({
          message: response.message,
          type: "warning"
        });
      }
    });
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      this.listQuery.dateStr = parseTime(this.date, "{y}-{m}-{d}");
      paymentList(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
          this.listLoading = false;
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
          this.listLoading = false;
        }
      });
    },
    handleFilter() {
      this.listQuery.pageNo = 1;
      this.fetchData();
    },
    handle(row) {
      console.log(row);
    }
  }
};
</script>
